.uprogress {
  top: 0;
  overflow: hidden;
  height: 8px;
  right: 0;
  left: 0;
  z-index: 1000;
}

.uprogress .blur {
  width: 100px;
  height: 100%;
  position: absolute;
  right: 1px;
  -webkit-box-shadow: 0 0 8px #006be5, 0 0 4px #006be5;
          box-shadow: 0 0 8px #006be5, 0 0 4px #006be5;
  -webkit-transform: rotate(3deg) translateY(-3px);
     -moz-transform: rotate(3deg) translateY(-3px);
       -o-transform: rotate(3deg) translateY(-3px);
          transform: rotate(3deg) translateY(-3px);
}

.uprogress.rtl .blur {
  left: 1px;
  -webkit-transform: rotate(-3deg) translateY(-3px);
     -moz-transform: rotate(-3deg) translateY(-3px);
       -o-transform: rotate(-3deg) translateY(-3px);
          transform: rotate(-3deg) translateY(-3px);
}

.uprogress .bar {
  height: 2px;
  position: absolute;
  background-color: #006be5;
  pointer-events: none;
  will-change: transform;
  top: 0;
}
